.dark_mode {
    position: absolute;
    top: -13px;
    right: 0;
    margin-right: 10px;
    transition: all 0.3s ease-in-out;
}

.dark_mode_label {
    width: 65px;
    height: 30px;
    position: relative;
    display: block;
    background: #ebebeb;
    border-radius: 200px;
    box-shadow: inset 0px 5px 15px rgba(0, 0, 0, 0.4), inset 0px -5px 15px rgba(255, 255, 255, 0.4);
    cursor: pointer;
    transition: 0.3s;
}

.dark_mode_label:after {
    content: '';
    width: 25px;
    height: 25px;
    position: absolute;
    top: 3px;
    left: 3px;
    background: linear-gradient(180deg, #ffcc89, #d8860b);
    border-radius: 180px;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
    transition: 0.3s;
}

.dark_mode_input {
    width: 0;
    height: 0;
    visibility: hidden;
}

.dark_mode_input:checked+.dark_mode_label {
    background: #242424;
}

.dark_mode_input:checked+.dark_mode_label:after {
    left: 62px;
    transform: translateX(-100%);
    background: linear-gradient(180deg, #777, #3a3a3a);
}

.dark_mode_label:active:after {
    width: 30px;
}

.dark_mode_label svg {
    position: absolute;
    width: 20px;
    top: 5px;
    z-index: 100;
}

.dark_mode_label svg.sun {
    left: 5px;
    fill: #fff;
    transition: 0.3s;
}

.dark_mode_label svg.moon {
    left: 40px;
    fill: #7e7e7e;
    transition: 0.3s;
}

.dark_mode_input:checked+.dark_mode_label svg.sun {
    fill: #7e7e7e;
}

.dark_mode_input:checked+.dark_mode_label svg.moon {
    fill: #fff;
}

@media screen and (max-width: 1200px) {
    .dark_mode {
        top: 48px;
    }
}

@media screen and (max-width: 740px) {
    .dark_mode {
        position: fixed;
        top: 1px;
        right: 40%;
        z-index: 99;
    }
}

@media (max-width: 390px) {
    .dark_mode {
        position: fixed;
        top: -13px;
    }
    .dark_mode_label {
        width: 50px;
        height: 21px;
    }
    .dark_mode_label svg {
        position: absolute;
        width: 14px;
        top: 3px;
        z-index: 100;
    }
    .dark_mode_label svg.sun {
        left: 5px;
    }
    .dark_mode_label svg.moon {
        left: 33px;
    }
    .dark_mode_input:checked+.dark_mode_label:after {
        left: 50px;
    }
    .dark_mode_label:after {
        width: 23px;
        height: 21px;
        top: 0;
        left: 1px;
    }
}